import React, { Component } from 'react';
import { Tree, Button, Dropdown, Menu } from 'antd';
import {
    PlusOutlined,
    DeleteOutlined,
    GlobalOutlined,
    CodeSandboxOutlined,
    BranchesOutlined,
    CodepenOutlined,
    EnvironmentOutlined,
    IdcardOutlined
} from '@ant-design/icons';
import './SiderMenu.less';

export default class SiderMenu extends Component {
    constructor(props) {
        super(props);
        let selectedKeys = props.selectedKeys || props.defaultSelectedKeys;
        let data = props.data || props.defaultData;
        this.state = {
            selectedKeys,
            data
        };
        this.menu = (
            <Menu onClick={this.handleClick}>
                <Menu.Item key="Map" icon={this.getIcon('Map')}>Map</Menu.Item>
                <Menu.Item key="BuildingLayer" icon={this.getIcon('BuildingLayer')}>BuildingLayer</Menu.Item>
                <Menu.Item key="LineLayer" icon={this.getIcon('LineLayer')}>LineLayer</Menu.Item>
                <Menu.Item key="PloygnLayer" icon={this.getIcon('PloygnLayer')}>PloygnLayer</Menu.Item>
                <Menu.Item key="PointLayer" icon={this.getIcon('PointLayer')}>PointLayer</Menu.Item>
                <Menu.Item key="LabelLayer" icon={this.getIcon('LabelLayer')}>LabelLayer</Menu.Item>
            </Menu>
        );
    }
    componentDidMount() {
    }
    componentWillReceiveProps(nextProps) {

    }
    getIcon(type) {
        if(type === 'Map') {
            return <GlobalOutlined />;
        } else if(type === 'BuildingLayer') {
            return <CodeSandboxOutlined />;
        } else if(type === 'LineLayer') {
            return <BranchesOutlined />;
        } else if(type === 'PloygnLayer') {
            return <CodepenOutlined />;
        } else if(type === 'PointLayer') {
            return <EnvironmentOutlined />;
        } else if(type === 'LabelLayer') {
            return <IdcardOutlined />;
        }
    }
    handleClick = param => {
        const item = {
            key: new Date().getTime(),
            title: param.key,
            icon: this.getIcon(param.key),
            isLeaf: false
        };
        if(this.props.onChange) {
            this.props.onChange(item);
        } else {
            const data = this.state.data.slice();
            data.unshift(item);
            this.setState({
                data,
                selectedKeys: [item.key]
            });
        }
    }
    // onExpand = expandedKeys => {
    //     this.setState({
    //         expandedKeys,
    //         //autoExpandParent: false,
    //     });
    // };
    render() {
        return (
            <div className="sider-main">
                {/* <div style={{
                    padding: '10px',
                    overflow: 'hidden',
                    borderBottom: '1px solid #ddd'
                }}>
                    <div style={{ float: 'right' }}>
                        <span style={{ margin: '0 5px' }}>
                            <Dropdown overlay={this.menu} trigger={['click']}>
                                <Button icon={<PlusOutlined />} size="small"/>
                            </Dropdown>
                        </span>
                        <span style={{ margin: '0 5px' }}>
                            <Button icon={<DeleteOutlined />} size="small"/>
                        </span>
                    </div>
                </div>
                <div style={{
                    padding: '10px',
                    overflow: 'hidden'
                }}>
                    <Tree
                        showIcon
                        //onExpand={this.onExpand}
                        selectedKeys={this.state.selectedKeys}
                        treeData={this.state.data}
                    />
                </div> */}
            </div>
        );
    }
}