import React, { Component } from 'react';
import IndexedDB from '@/storage/IndexedDB';
import publish from '@/file/Zip';
import './index.less';


class TestPage extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        this.db = new IndexedDB().init(() => {
            this.db.get(state => {
                console.log(state);
            });
        });
    }
    handleClick = type => {
        return e => {
            e.preventDefault();
            if(type === 'update') {
                this.db.set({
                    dsc: new Date().getTime()
                });
            } else if(type === 'clear') {
                this.db.clear();
            } else if(type === 'publish') {
                publish();
            }
            this.db.get(state => {
                console.log(state);
            });
        };
    }
    render() {
        return (
            <div>
                <button onClick={ this.handleClick('update') }>update</button>
                <button onClick={ this.handleClick('clear') }>clear</button>
                <button onClick={ this.handleClick('publish') }>publish</button>
            </div>
        );
    }
};
export default TestPage;