import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Form, Button, Input, Message } from '../../dw-rui';
import history from '../../utils/history';
import Ajax from '../../utils/Ajax';
import './index.less';

@connect(
    state => state.login,
    dispatch => bindActionCreators({
        login: param => {
            return dispatch => {
                // // Ajax(`platform/login`, {
                // //     method: 'POST',
                // //     body: JSON.stringify(param)
                // // }).then(rs => {
                        
                // //     if(rs.code === 0) {
                //         const rs = {
                //             data: {
                //                 userName: 'admin',
                //                 password: 'admin'
                //             },
                //             token: 'sdfertertdfgdfgdf'
                //         };
                //         localStorage.setItem('user', JSON.stringify(rs.data));
                //         localStorage.setItem('token', rs.token);
                //         dispatch({
                //             type: "login",
                //             user: rs.data
                //         });
                //         history.push('/');
                // //     } else {
                // //         // Message.warning({
                // //         //     msg: <div>用户名或密码错误！</div>
                // //         // });
                // //     }
                // // });

                Ajax(`platform/login`, {
                    method: 'POST',
                    body: JSON.stringify(param)
                }).then(rs => {
                    if(rs.code === 0) {
                        localStorage.setItem('user', JSON.stringify(rs.data));
                        localStorage.setItem('token', rs.token);
                        dispatch({
                            type: "login",
                            user: rs.data
                        });
                        history.push('/');
                    } else {
                        Message.warning({
                            msg: <div>用户名或密码错误！</div>
                        });
                    }
                });
            }
        }
    }, dispatch)
) 
class LoginPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: '',
            password: ''
        };
    }
    componentDidMount() {
    }
    handleChange = (e) => {
        e.preventDefault();
        this.setState({ [e.target.name]: e.target.value });
    };
    handleSubmit = e => {
        e.preventDefault();
        if (this.state.userName == '' || this.state.password == '') {
            Message.warning({
                msg: <div>用户名或密码错误！</div>
            });
            return;
        }
        this.props.login(this.state);
    }
    render() {
        return (
            <div className="login" onSubmit={this.submit}>
                <Form onSubmit={this.handleSubmit} className="login-form">
                    <div style={{ padding: '10px' }}>
                        <Input type="text" name="userName" value={this.state.userName} onChange={this.handleChange} placeholder="请输入admin" />
                    </div>
                    <div style={{ padding: '10px' }}>
                        <Input type="password" name="password" value={this.state.password} onChange={this.handleChange} placeholder="请输入admin" />
                    </div>
                    <div style={{ padding: '10px' }}>
                        <Button type="submit">Login In</Button>
                    </div>
                </Form>
            </div>
        );
    }
};
export default LoginPage;