import * as THREE from 'three';
// import { JSZip } from './jszip';

const link = document.createElement('a');
const save = (blob, filename) => {
    link.href = URL.createObjectURL( blob );
    link.download = filename || 'data.json';
    link.dispatchEvent(new MouseEvent('click'));
    // URL.revokeObjectURL( url ); breaks Firefox...
}
//const zip = new window.JSZip();
let zip = {};
const publish = function() {
    let output = {
        ret: true,
        data: []
    };
    output = JSON.stringify(output, (key, value) => {
		const precision = 6;
		return typeof value === 'number' ? parseFloat(value.toFixed(precision)) : value;
	}, '\t');
    output = output.replace(/[\n\t]+([\d\.e\-\[\]]+)/g, '$1');

    zip.file('app.json', output);

    const title = 'TEST';

    const manager = new THREE.LoadingManager(() => {
        save(zip.generate({ type: 'blob' }), `${title}.zip`);
    });
    const loader = new THREE.FileLoader(manager);

    loader.load('template/index.html', content => {
        content = content.replace('<!-- title -->', title);
        const includes = [];
        content = content.replace('<!-- includes -->', includes.join('\n\t\t'));
        //const editButton = '';
        // if (config.getKey('project/editable')) {
        //     editButton = [
        //         '',
        //         '			var button = document.createElement( \'a\' );',
        //         '			button.href = \'https://threejs.org/editor/#file=\' + location.href.split( \'/\' ).slice( 0, - 1 ).join( \'/\' ) + \'/app.json\';',
        //         '			button.style.cssText = \'position: absolute; bottom: 20px; right: 20px; padding: 10px 16px; color: #fff; border: 1px solid #fff; border-radius: 20px; text-decoration: none;\';',
        //         '			button.target = \'_blank\';',
        //         '			button.textContent = \'EDIT\';',
        //         '			document.body.appendChild( button );',
        //         ''
        //     ].join('\n');
        // }
        //content = content.replace('\n\t\t\t/* edit button */\n', editButton);
        zip.file('index.html', content);
    });
    loader.load('template/app.js', content => {
        zip.file('js/app.js', content);
    });
    loader.load('template/threeJS.js', content => {
        zip.file('js/threeJS.js', content);
    });
    loader.load('template/THREEx.EffectManager.js', content => {
        zip.file('js/THREEx.EffectManager.js', content);
    });
}
export default publish;